import React from 'react';
import { UniformContext } from '../contexts/UniformContext';

export function withUniformContext<T>(Component: React.ComponentClass<T> | React.SFC<T>) {
    return function ComponentWithContext(props: T) {
        return (
            <UniformContext.Consumer>
                {(context) => <Component {...props} uniformContext={context} />}
            </UniformContext.Consumer>
        );
    };
}
